import { Link as RouterLink } from 'react-router-dom';
import faker from 'faker';
import { Icon } from '@iconify/react';
import PlusFill from '@iconify/icons-eva/plus-fill';
// material
import { Box, Button, Container } from '@mui/material';

import useSettings from '../hooks/useSettings';

import { PATH_DASHBOARD } from '../routes/paths';

import Page from '../components/Page';
import HeaderBreadcrumbs from '../components/HeaderBreadcrumbs';
import NotificationListTable from '../components/notification/NotificationListTable';

// ----------------------------------------------------------------------

const NOTIFICATION_TYPES = ['Image', 'Text'];
const TYPES = ['Post', 'Bonus', 'Rewards', 'User'];
const notificationListData = [...Array(10)].map((_, index) => {
  const setIndex = index + 1;
  return {
    id: `id ${setIndex}`,
    name: faker.lorem.word(),
    notificationType: faker.helpers.randomize(NOTIFICATION_TYPES),
    type: faker.helpers.randomize(TYPES),
    users: [...Array(faker.datatype.number({ min: 1, max: 12 }))].map((_, index) => {
      const setIndex = index + 1;
      return {
        id: `id ${setIndex}`,
        username: faker.internet.userName(),
      };
    }),
    dateTime: faker.date.recent(),
  };
});
const NotificationList = () => {
  const { themeStretch } = useSettings();

  return (
    <Page title="Notification: List">
      <Container maxWidth={themeStretch ? false : 'lg'}>
        <HeaderBreadcrumbs
          heading="Notification List"
          links={[{ name: 'Notification', href: PATH_DASHBOARD.notification.list }, { name: 'List' }]}
          action={
            <Button
              variant="contained"
              component={RouterLink}
              to={PATH_DASHBOARD.notification.create}
              startIcon={<Icon icon={PlusFill} />}
            >
              New Notification
            </Button>
          }
        />
        <Box sx={{ height: 720 }}>
          <NotificationListTable notificationList={notificationListData} />
        </Box>
      </Container>
    </Page>
  );
};

export default NotificationList;
