import PropTypes from 'prop-types';

import { Link, Table, TableBody, TableCell, TableContainer, TableHead, TableRow } from '@mui/material';
import { Link as RouterLink } from 'react-router-dom';
import format from 'date-fns/format';

import Scrollbar from '../Scrollbar';
import { PATH_DASHBOARD } from '../../routes/paths';

const TABLE_HEAD = [
  { id: 'name', label: 'Name', alignRight: false },
  { id: 'notificationType', label: 'Notification Type', alignRight: false },
  { id: 'type', label: 'Type', alignRight: false },
  { id: 'users', label: 'Total Users', alignRight: false },
  { id: 'dateTime', label: 'Date Time', alignRight: false },
  { id: 'action', label: 'Action', alignRight: false },
];

NotificationListTable.propTypes = {
  notificationList: PropTypes.array,
};

function NotificationListTable({ notificationList = [] }) {
  return (
    <Scrollbar>
      <TableContainer sx={{ minWidth: 800 }}>
        <Table aria-label="notification table">
          <TableHead>
            <TableRow>
              {TABLE_HEAD.map((headCell) => (
                <TableCell key={headCell.id} align={headCell.alignRight ? 'right' : 'left'}>
                  {headCell.label}
                </TableCell>
              ))}
            </TableRow>
          </TableHead>
          <TableBody>
            {notificationList.map((row) => (
              <TableRow key={row.id}>
                <TableCell>{row.name}</TableCell>
                <TableCell>{row.notificationType}</TableCell>
                <TableCell>{row.type}</TableCell>
                <TableCell>{row?.users?.length}</TableCell>
                <TableCell>{format(row.dateTime, 'MM/dd/yyyy HH:MM')}</TableCell>
                <TableCell>
                  <Link to={PATH_DASHBOARD.notification.list} component={RouterLink}>
                    User List
                  </Link>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
    </Scrollbar>
  );
}

export default NotificationListTable;
